body {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

}

.title {
  position: absolute;
  top: 5px;
  left: 5px;
  font-size: large;
  font-weight: 800;
}
.title span {
  letter-spacing: 0.3em;
}
.inputs {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 10px 0px;
}

.hex {
  border: 0;
  outline: 0;
  border-bottom: 1px solid grey;
  height: 30px;
}


.inputs button {
  border: none;
  background: none;
  color:black;
  height: 30px;
  font-size: 1em;
  margin: 0px ;
  text-transform: uppercase;
  font-weight: lighter;

}

button:hover {
  cursor: pointer;
  color: lightgrey;

}

button:focus {
  outline: none;
  border: none;
}

.color {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 500px;
  width: 70%;
  background-color: #FF0000;
}

.color-text {
  color: #00ffff;
  letter-spacing: 0.2em;
  margin-top: 0;
  font-size: 2em;
}

.uppercase-txt {
  text-transform: uppercase;
}

footer {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

footer p {
  margin: 0px;
  letter-spacing: 0.3em;
}


@media only screen and (max-width: 680px ) {
  .inputs {
      margin: 5px 0px;
  }
  .hex {
    width: 50%;
  }
  .btn-block {
      margin-top: 5px;
  }

  .color-text {
    font-size: 1.5em;
  }
  
}

@media only screen and (max-width: 417px) {
  .inputs button {
      font-size: 0.7em;
  }

  .color-text {
    font-size: 1em;
  }
}

@media only screen and (max-height: 666px ) {
  .color {
      height: 70vh;
  }


}

